import axios from './configApi';

const ReportService = {
    getTeachers: async (filters = null) => {
        try {
            let route = '/reports/teachers';
            if (filters) {
                route += '?';
                Object.keys(filters).forEach((key, index) => {
                    route += `${index > 0 ? '&' : ''}${key}=${filters[key]}`;
                });
            }
            const response = await axios.get(route);
            return {
                ok: true,
                data: response.data
            };
        } catch (error) {
            return { ok: false };
        }
    },
    getStudents: async (filters = null) => {
        try {
            let route = '/reports/students';
            if (filters) {
                route += '?';
                Object.keys(filters).forEach((key, index) => {
                    route += `${index > 0 ? '&' : ''}${key}=${filters[key]}`;
                });
            }
            const response = await axios.get(route);
            return {
                ok: true,
                data: response.data
            };
        } catch (error) {
            return { ok: false };
        }
    },
    generatePdfGetUrl: async (reports) => {
        try {
            const response = await axios.post('/reports/attendance/generate-pdf/url', { reports: reports });

            if (response.status !== 200) {
                throw new Error('Server responded with an error');
            }

            return {
                ok: true,
                data: response.data,
            };
        } catch (error) {
            return {
                ok: false,
                error: error.message,
            };
        }
    },
}

export default ReportService